.fh5co-cover .display-tc .fh5co-social-icons li a:hover {
    text-decoration: underline;
}

#fh5co-header .display-tc .fh5co-social-icons li a i,
.fh5co-cover .display-tc .fh5co-social-icons li a i {
    font-size: 30px;
}

