body {
    font-family: "Space Mono", Arial, serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.7;
    color: #4d4d4d;
    background: #fff;
}

#page {
    position: relative;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}
.offcanvas #page {
    overflow: hidden;
    position: absolute;
}
.offcanvas #page:after {
    -webkit-transition: 2s;
    -o-transition: 2s;
    transition: 2s;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 101;
    background: rgba(0, 0, 0, 0.7);
    content: "";
}

a {
    color: #3c9d9b;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
}
a:hover,
a:active,
a:focus {
    color: #3c9d9b;
    outline: none;
    text-decoration: none;
}

p {
    margin-bottom: 30px;
}

h1,
h2,
h3,
h4,
h5,
h6,
figure {
    color: #000;
    font-family: "Space Mono", Arial, serif;
    font-weight: 400;
    margin: 0 0 20px 0;
}

::-webkit-selection {
    color: #fff;
    background: #3c9d9b;
}

::-moz-selection {
    color: #fff;
    background: #3c9d9b;
}

::selection {
    color: #fff;
    background: #3c9d9b;
}

#fh5co-header,
.fh5co-cover {
    background-color: transparent;
    background-size: cover;
    background-attachment: fixed;
    position: relative;
    height: 600px;
    width: 100%;
}
#fh5co-header .overlay,
.fh5co-cover .overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgb(60, 157, 155, 0.8);
}
#fh5co-header .display-t,
.fh5co-cover .display-t {
    width: 100%;
    display: table;
}
#fh5co-header .display-tc,
.fh5co-cover .display-tc {
    display: table-cell;
    vertical-align: middle;
    height: 600px;
}
#fh5co-header .display-tc h1,
#fh5co-header .display-tc h2,
#fh5co-header .display-tc h3,
.fh5co-cover .display-tc h1,
.fh5co-cover .display-tc h2,
.fh5co-cover .display-tc h3 {
    margin: 0;
    padding: 0;
    color: white;
}
#fh5co-header .display-tc h1,
.fh5co-cover .display-tc h1 {
    font-family: "Kaushan Script", cursive;
    margin-bottom: 30px;
    font-size: 50px;
    line-height: 1.3;
    font-weight: 300;
    -webkit-transform: rotate(-5deg);
    -moz-transform: rotate(-5deg);
    -ms-transform: rotate(-5deg);
    -o-transform: rotate(-5deg);
    transform: rotate(-5deg);
}
#fh5co-header .display-tc h1 span,
.fh5co-cover .display-tc h1 span {
    padding: 4px 15px;
    position: relative;
}
#fh5co-header .display-tc h1 span:before,
.fh5co-cover .display-tc h1 span:before {
    position: absolute;
    top: 40px;
    left: 0;
    width: 30px;
    height: 4px;
    content: "";
    background: #fff;
    margin-left: -30px;
}
#fh5co-header .display-tc h1 span:after,
.fh5co-cover .display-tc h1 span:after {
    position: absolute;
    top: 40px;
    right: 0;
    width: 30px;
    height: 4px;
    content: "";
    background: #fff;
    margin-right: -30px;
}
@media screen and (max-width: 768px) {
    #fh5co-header .display-tc h1,
    .fh5co-cover .display-tc h1 {
        font-size: 34px;
    }
    #fh5co-header .display-tc h1 span:before,
    .fh5co-cover .display-tc h1 span:before {
        top: 28px;
        width: 20px;
        height: 3px;
        margin-left: -15px;
    }
    #fh5co-header .display-tc h1 span:after,
    .fh5co-cover .display-tc h1 span:after {
        top: 28px;
        width: 20px;
        height: 3px;
        margin-right: -15px;
    }
}
#fh5co-header .display-tc h2,
.fh5co-cover .display-tc h2 {
    font-size: 20px;
    line-height: 1.5;
    margin-bottom: 30px;
}
#fh5co-header .display-tc h3,
.fh5co-cover .display-tc h3 {
    font-size: 16px;
}
@media screen and (max-width: 768px) {
